            createdAt: new Date(),
            _localId: null,
            immutable: false,
            name,
            owner: actor.userAccount,
            // platformConfig: platformConfig ? JSON.stringify(platformConfig) : null,
            // platformConfig: null,
            repositoryTransactionHistory: [],
            // FIXME: propage the 
            source: actor.application.fullName,
            GUID: v4(),
